<template>
  <div class="visitorsApply">
    <van-nav-bar :title="navTitle" left-arrow fixed @click-left="$router.back();"  />
    <div class="mescroll">
      <div class="top">
        <img src="..\..\assets\img\approveIcon\bg2.jpg" alt="">
      </div>
      <div class="content">
        <div class="mid">
          <van-cell-group :border="false">
            <van-field v-model="name" clearable placeholder="请填写您的姓名" @blur="blur()" :border="false" required="true"/>
            <van-field v-model="telno" clearable placeholder="请填写您的手机号" @blur="blur()" :border="false" required="true"/>
            <!-- <van-field v-model="IdNumber" clearable placeholder="请填写您的身份证号" @blur="blur()" :border="false" /> -->
            <van-field v-model="department1" clearable placeholder="请填写您的单位" @blur="blur()" :border="false" />
            <van-field v-model="name2" clearable placeholder="请填写您搭档的姓名" @blur="blur()" :border="false" />
            <van-field v-model="telno2" clearable placeholder="请填写您搭档的手机号" @blur="blur()" :border="false" />
          </van-cell-group>
          <p class="text">* 单位可填可不填；妇联女子体育文化节参赛选手应填写单位。</p>
          <p class="text">* 个人报名至少填写前2项即可，组合报名至少填写两个人的姓名和联系电话。</p>
        </div>
        <div class="agreement" @click="open">
            <van-checkbox v-model="checked"></van-checkbox>
            <p >
              阅读并同意
              <span>《健康承诺书》</span>
            </p>
        </div>
        <div class="agreement" @click="open2">
           <p> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  大赛<span>《奖项设置》</span></p>
        </div>
        <div class="submit" @click="apply()">立即报名</div>
        <p class="prompt">温馨提示</p>
        <p class="text">1) 经本人确认报名后，不得更换他人参赛。</p>
        <p class="text">2) 因疫情防控需要，所有选手必须签订健康承诺书，赛前14天内前往中高风险地区的取消比赛资格。参赛时主动出示“苏康码”，经工作人员查验和测量体温正常方可进入比赛场地。</p>
        <p class="text">3）本次大赛设立组委会和仲裁委员会，未尽事宜由组委会负责解释。</p> 
        <p class="text">4）咨询电话：13605261169</p>
      </div>
    </div>
    <van-popup v-model="agreementShow">
      <div class="agreementMid">
        <div class="close" @click="agreementShow=false">×</div>
        <div v-html="agreement">{{agreement}}</div>
      </div>
    </van-popup>
    <van-popup v-model="agreementShow2">
      <div class="agreementMid">
        <div class="close" @click="agreementShow2=false">×</div>
        <div v-html="agreement2">{{agreement2}}</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import {
  NavBar,
  Field,
  Cell,
  CellGroup,
  Popup,
  DatetimePicker, 
  Search, 
  Checkbox,
  Dialog
} from "vant";
import {  login, guandanPlayApply,getPage } from "@/api";
export default {
  components: {
    [NavBar.name]: NavBar,
    [Field.name]: Field,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Popup.name]: Popup, 
    [Search.name]: Search, 
    [Checkbox.name]: Checkbox,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      navTitle: "",
      name: "",
      telno: "", 
      IdNumber: "",
      department1: "",
      name2: "",
      telno2: "", 
      checked: false,
      popupShow: false,
      agreementShow: false,
      agreement: "",
      agreementShow2: false,
      agreement2: "",
      min: 0,
      Interval: null,
    };
  },
  methods: {
    open() {
      if (!this.checked) {
        this.$toast("请选同意健康承诺书");
        return;
      }
      this.agreementShow = true; 
    },
    onClose() {
      this.agreementShow = false;
      this.agreementShow2 = fasle;
    },
    open2() { 
      this.agreementShow2 = true; 
    },
    blur() {
      document.body.scrollTop = 0;
    }, 
    apply() {
      if (!this.name) {
        this.$toast("请填写您的姓名");
        return;
      }
      if (!this.telno) {
        this.$toast("请填写您的手机号");
        return;
      }
      if (!this.$utils.isMobile(this.telno)) {
        this.$toast("手机号码格式不正确");
        return;
      }
      // if (!this.IdNumber) {
      //   this.$toast("请填写您的身份证号");
      //   return;
      // }
      // if (!this.$utils.isIdCard(this.IdNumber)) {
      //   this.$toast("身份证号码格式不正确");
      //   return;
      // }
      // if (!this.department1) {
      //   this.$toast("请填写您的单位");
      //   return;
      // }
      if (!this.checked) {
        this.$toast("请先同意《健康承诺书》");
        return;
      }
      this.$toast.allowMultiple();
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      let dataObj = {
        playUserId: sessionStorage.getItem("userId"),
        //playOpenId: this.$route.query.openId,
        playName: this.name,
        playMobile: this.telno,
        playIdNumber: '',//this.IdNumber,
        playDept: this.department1,
        playSex: '',//this.$route.query.visitSex,
        playName2: this.name2,
        playMobile2 : this.telno2, 
        playSex2:'',
      };
      guandanPlayApply(dataObj).then(res => {
        loading.clear();
        if (res.code == "0") {
          setTimeout(() => {
            this.$router.push({
              name: "guandanRegisterSuccess",
              query: {
                surveyeName: sessionStorage.getItem("name"),
              },
            });
          });
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持`续展示 toast
        forbidClick: true // 禁用背景点击
      }); 
      getPage({ id: "F35A0E71D4504F15B7470C7B1B1D0BB9" }).then(res => {
        if (res.code == "0") { 
           this.agreement = res.data.tMsg;
           this.$toast("欢迎报名掼蛋大赛，请认真阅读健康承诺书。");
        } else {
          this.$toast(res.msg);
        }
      });
      getPage({ id: "C3F32800E1D54EB3848B06C028F09ED4" }).then(res => {
        if (res.code == "0") { 
           this.agreement2 = res.data.tMsg;
           this.$toast("欢迎报名掼蛋大赛，查看丰盛的大赛奖项设置。");
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    this.navTitle = "掼蛋报名";
    this.loadData();
  },
};
</script>
